import React from "react";
import styles from "./Footer.module.css";

const footer = (props) => {
    return(
        <footer class={styles.footer}>
        <div class="footer__links">
            <ul class={styles.footer_list}>
                <li class={styles.footer_item}><a href="#" class={styles.footer_item_link}>Career</a></li>
                <li class={styles.footer_item}><a href="#" class={styles.footer_item_link}>Legal Notice</a></li>
                <li class={styles.footer_item}><a href="https://yusufmert.netlify.com" target="_blank"class={styles.footer_item_link}>Contact</a></li>       
            </ul>
        </div>
        <div class={styles.logo_box}> 
            <img src={require("../../assets/logo.png")} alt="logo" class={styles.logo}/>
        </div>
        <div class={styles.text}>
            <p class={styles.copyright}>
                    Built by Yusuf Mert Çakmak. Copyright © by Yusuf Mert Çakmak. You are 100% allowed to use this webpage for both personal and commercial use, but NOT to claim it as your own design. A credit to the original author, Yusuf Mert Çakmak, is of course highly appreciated!</p>
        </div>
    </footer>        
    )
}

export default footer;