import React from "react";
import styles from "./Nav.module.css"
const nav = (props) => {
    return(
        <div class={styles.heading}>
            <div class={styles.logobox}>
                <a href="#"><img src={require("../../assets/logo.png")} alt="" class={styles.logo}/></a>
            </div>
            <div class={styles.nav}>
                <ul class={styles.list}>
                    <li class={styles.list_item}><a href="#" class={styles.nav_link}>Home</a></li>
                    <li class={styles.list_item}><a href="#rooms" class={styles.nav_link}>Rooms</a></li>
                    <li class={styles.list_item}><a href="https://yusufmert.netlify.com" target="_blank" class={styles.nav_link}>Creator</a></li>
                </ul>
            </div>
        </div>
       
    )
}

export default nav;