import React from "react";
import styles from "./Intro.module.css";
const intro = (props) => {
    
    return(
        <div className={styles.Intro}>
            <div className={styles.inner}>
                <div><h3 className={styles.inner_header}>Get Luxury Room</h3></div>
                <div><h4 className={styles.inner_linktext}><a className={styles.inner_link} href="#rooms">Rooms</a></h4></div>
            </div>
        </div>
    )
}

export default intro;
