import React from "react";
import styles from "./Rooms.module.css"
const style1=styles.card + " " + styles.card_2;
const style2=styles.card + " " + styles.card_1;
const style3=styles.card + " " + styles.card_3;


const Rooms = (props) =>{
    return(
        
        <div class={styles.rooms_container}>
                <div class={styles.rooms} id="rooms">
                    <div class={style1}>
                        <div class={styles.card_text}>Single Room</div>
                        <div class={styles.card_price}>160$</div>
                        <div class={styles.card_buy}><a href="#" class={styles.card_buy_link}>Buy Now</a></div>
                    </div>
                    
                    <div class={style2}>
                        <div class={styles.card_text}>Family Room</div>
                        <div class={styles.card_price}>200$</div>
                        <div class={styles.card_buy}><a href="#" class={styles.card_buy_link}>Buy Now</a></div>
                    </div>
                    <div class={style3}>
                        <div class={styles.card_text}>Suite Room</div>
                        <div class={styles.card_price}>300$</div>
                        <div class={styles.card_buy}><a href="#" class={styles.card_buy_link}>Buy Now</a></div>
                    </div>
                </div>
            </div>
    )
}

export default Rooms;